@media (min-width: 992px) {
  .container-nav {
    margin-top: 90px;
    overflow: hidden;
  }
}
.header-site {
  background-color: #e1e3da;
  padding-top: 15px;
}
.jump-to {
  position: absolute;
  z-index: 999999;
}
.jump-to > .link {
  width: 1px;
  height: 1px;
  padding: 5px;
  left: -10000px;
  background-color: #7d965d;
  position: absolute;
  overflow: hidden;
}
.jump-to > .link:focus,
.jump-to > .link:active,
.jump-to > .link:hover {
  width: auto;
  height: auto;
  left: 0;
  color: #fff;
  text-decoration: none;
  position: static;
  overflow: visible;
}
.logo-site {
  display: inline-block;
  padding-bottom: 15px;
}
.nav-site > .item > .link {
  font-size: 14px;
  font-weight: bold;
  color: #333;
  text-decoration: none;
  text-transform: uppercase;
  position: relative;
  display: block;
}
@media (min-width: 992px) {
  .nav-site {
    text-align: right;
  }
  .nav-site > .item {
    display: inline-block;
    margin-right: 15px;
  }
  .nav-site > .item:last-child {
    margin-right: 0;
  }
  .nav-site > .item > .link {
    padding-bottom: 15px;
  }
  .nav-site > .item > .link:after {
    content: '';
    width: 100%;
    height: 6px;
    bottom: 0;
    left: 0;
    background-color: #848973;
    transform: translateY(6px);
    transform-origin: bottom;
    transition: 0.2s all linear;
    position: absolute;
  }
  .nav-site > .item > .link:hover,
  .nav-site > .item > .link:focus,
  .nav-site > .item > .link:active {
    color: #848973;
    outline: 0;
  }
  .nav-site > .item > .link:hover:after,
  .nav-site > .item > .link:focus:after,
  .nav-site > .item > .link:active:after {
    transform: translateY(0);
  }
}
@media (max-width: 991px) {
  .nav-site {
    width: 250px;
    height: 100%;
    min-height: 100vh;
    min-height: 100vmax;
    top: 0;
    right: 0;
    background-color: #e1e3da;
    transform: translateX(250px);
    display: none;
    position: fixed;
  }
  .nav-site.js-active {
    display: block;
  }
  .nav-site > .item > .link {
    padding: 15px;
  }
  .nav-site > .item > .link:hover,
  .nav-site > .item > .link:focus,
  .nav-site > .item > .link:active {
    color: #fff;
    background-color: #848973;
    outline: 0;
  }
}
.navbar-control {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.navbar-control:not(.-close) {
  margin-top: 75px;
  margin-left: 20px;
}
.navbar-control > .text {
  margin: 0;
  font-size: 14px;
  color: #333;
}
.navbar-toggle {
  width: 52px;
  height: 40px;
  padding: 9px 10px;
  position: relative;
}
.navbar-toggle > .icon-bar {
  display: block;
  width: 30px;
  height: 3px;
  border-radius: 4px;
  background-color: #333;
  transition: all 0.2s linear;
}
.navbar-toggle > .icon-bar:nth-child(2) {
  opacity: 1;
}
.navbar-toggle .icon-bar + .icon-bar {
  margin-top: 4px;
}
.wrapper-site {
  transition: 0.4s transform ease-in-out;
}
.wrapper-site.js-active-menu {
  height: 100%;
  transition: 0.4s transform ease-in-out;
  transform: translateX(-250px);
}
.wrapper-site.js-active-menu .navbar-toggle > .icon-bar:nth-child(1) {
  transform: rotate(45deg);
}
.wrapper-site.js-active-menu .navbar-toggle > .icon-bar:nth-child(2) {
  width: 0;
  height: 0;
  margin: 0;
  opacity: 0;
}
.wrapper-site.js-active-menu .navbar-toggle > .icon-bar:nth-child(3) {
  margin-top: -3px;
  transform: rotate(-45deg);
}
